<template>
    <div>
        <article class="passJoinBox certificationBox termination">
            <div class="topBox">
                <h1 class="title">
                    Pass by KT <span>세이프가드 해지</span>
                </h1>
                <div class="con1">
                    <p class="text2">
                        안전한 금융거래를 위해서 <br>
                        KT 세이프가드를 <br>
                        <span>계속 이용해 보세요<font>!</font></span>
                    </p>
                </div><!-- con1 end -->
				<!--====================================================== step 1 ===========================================================-->
				<div class="step1">
					<div class="sec2">
						<p class="remark1">
							해지를 위해서는 가입자 확인이 필요합니다.
						</p>
						<div class="inputWrap ginputWrap">
							<input type="text"  maxlength="30" placeholder="성명 (홍길동)" v-model="userName">
						</div>
						<div class="inputWrap binputWrap">
							<input type="text"  maxlength="8" placeholder="생년월일 (19800101)" v-model="userBirthday">
						</div>
                        <div class="inputWrap binputWrap">
							<input type="text"  maxlength="11" placeholder="휴대폰 번호 ('-'제외)" v-model="userPhone">
						</div>
                        <ul class="cb genderUl">
                            <li class="checkBox" :class="{ on : man }" @click="checkGender(1)">남</li>
                            <li class="checkBox" :class="{ on : woman }" @click="checkGender(2)">여</li>
                        </ul>
					</div>
					
				</div>
				<!--====================================================== step 1 end ===========================================================-->

				<!--====================================================== step 2 ===============================================================-->
				<div class="step2">
					<div class="sec2">
                        <div class="inBtn" @click="startTimer()">인증번호 전송</div>
						<div class="inputWrap binputWrap">
							<input type="text" placeholder="인증번호 입력" v-model="keyNumber" maxlength="6" >
							<div class="timer">
								<span>{{ timeValue}}</span>
							</div>
						</div>
						<span class="resubmit" @click="reSubmit()">재전송</span>
					</div>
				</div>
				<!--====================================================== step 2 end ===========================================================-->
                <article class="cFooter cFooter2">
                    <div class="btnWrap btnWrap2 cb">
                        <div class="left" @click="backMove()">취소</div>
                        <div class="right kt" @click="checkForm1()">KT 세이프가드 해지</div>
                    </div>
                    <p class="ex">
                        * 해지 시 해당월 이용내역에 대해서는 일할 계산되어 청구됩니다.
                    </p>
                </article>
            </div>
        </article>
    </div>
</template>

<script>
export default {
	data() {
		return {
			// input 휴대폰 번호
			userPhone: '',

			// input 유저이름
			userName: '',

			// input 유저생일
			userBirthday: '',

			// input 성별체크
			man: true,
			woman: false,

			// 임시 유저 정보
			userData: {
				userName: 'ㄱ',
				userBirthday: '11111111',
				userPhone: '01111111111',
				man: true,
				woman: false,
				certificationNumber: '111111',
			},

			// 타이머 변수들
			keyNumber: '',
			timeValue: '3:00',
			test: true,
			already: true,
		};
	},

	watch: {
		//한글 입력만 먹히도록 하는 코드 (성명입력칸에서 숫자와 영어입력이 감지될때 replace를써서 ''으로 치환)
		userName() {
			return (this.userName = this.userName
				.replace(/[a-z]/gi, '')
				.replace(/[0-9]/gi, ''));
		},
		//숫자 입력만 먹히도록 하는 코드
		userBirthday() {
			return (this.userBirthday = this.userBirthday.replace(/[^0-9]/gi, ''));
		},
	},
	methods: {
		//성별체크
		checkGender(index) {
			switch (index) {
				// "남"을체크하면 "남"체크,"여"체크해제
				case 1:
					this.man = true;
					this.woman = false;
					break;

				// "여"를체크하면 "여"체크,"남"체크해제
				case 2:
					this.woman = true;
					this.man = false;
					break;
			}
		},

		// 휴대폰인증 타이머
		startTimer() {
			this.keyNumber = '';
			this.timeValue = '3:00';
			var timer = 180,
				minutes,
				seconds;
			var vd = this;
			if (this.already) {
				this.already = false;
				this.porling = setInterval(function() {
					minutes = parseInt(timer / 60, 10);
					seconds = parseInt(timer % 60, 10);

					minutes = minutes < 10 ? minutes : minutes;
					seconds = seconds < 10 ? '0' + seconds : seconds;

					vd.timeValue = minutes + ':' + seconds;

					if (--timer < 0) {
						timer = 0;
					}
				}, 1000);
			} else {
				alert('인증번호를 전송하였습니다.');
			}
		},

		checkForm1() {
			// 성명 체크
			if (this.userName == this.userData.userName) {
				console.log('sucsess');
			} else {
				alert('가입자정보 입력사항을 다시 한번 확인해주시기 바랍니다.');
				return false;
			}

			// 생년월일 체크
			if (this.userBirthday == this.userData.userBirthday) {
				console.log('sucsess');
			} else {
				alert('가입자정보 입력사항을 다시 한번 확인해주시기 바랍니다.');
				return false;
			}

			// 휴대폰번호 체크
			if (this.userPhone == this.userData.userPhone) {
				console.log('sucsess');
			} else {
				alert('가입자정보 입력사항을 다시 한번 확인해주시기 바랍니다.');
				return false;
			}

			// 성별 체크
			if (
				this.man == !this.userData.man &&
				this.woman == !this.userData.woman
			) {
				alert('가입자정보 입력사항을 다시 한번 확인해주시기 바랍니다.');
				return false;
			}

			// 인증번호 체크
			if (this.keyNumber == this.userData.certificationNumber) {
				console.log('sucsess');
			} else {
				alert('인증번호를 다시한번 확인해 주시기 바랍니다.');
				return false;
			}
		},

		backMove() {
			this.$router.replace('/');
		},

		back() {
			clearInterval(this.porling);
		},

		reSubmit() {
			this.already = true;
			clearInterval(this.porling);
			this.startTimer();
		},
	},
};
</script>

